import loadable from '@loadable/component';
import React, { ReactNode } from 'react';
import BaseComponent from '../../../base/base-component';
import { ILineChartProperties } from './line-chart-interface';

export default class LineChart extends BaseComponent<ILineChartProperties> {
  public render(): ReactNode {
    const { gradientColorStops, height, lineChartSerie, onToolTipShow, width } =
      this.props;

    const Chart = loadable(() => import('react-apexcharts'));
    const categories: string[] = [];
    const yValues: number[] = [];

    lineChartSerie.chartData.forEach((item) => {
      categories.push(item.x);
      yValues.push(item.y);
    });
    return (
      <Chart
        options={{
          chart: {
            height: height,
            type: 'line',
            foreColor: '#6D6D6D',
            toolbar: {
              show: false,
            },
          },
          markers: {
            colors: ['#EBAF52'],
          },
          fill: {
            type: 'gradient',
            gradient: {
              shadeIntensity: 1,
              opacityFrom: 0.7,
              opacityTo: 0.9,
              colorStops: gradientColorStops.map((stop, index) => {
                return {
                  offset: (100 / gradientColorStops.length) * index,
                  color: stop,
                  opacity: 1,
                };
              }),
            },
          },
          xaxis: {
            categories,
            axisBorder: {
              color: '#7E7986',
            },
            labels: {
              style: {
                colors: '#7E7986',
                fontSize: '12px',
                fontFamily: 'IBM Plex Mono',
                fontWeight: 400,
              },
            },
          },
          yaxis: {
            show: true,
            showAlways: true,
            showForNullSeries: true,
            opposite: true,
            tickAmount: 1,
            min: Math.min(...yValues),
            max: Math.max(...yValues),
            forceNiceScale: true,
            axisBorder: {
              show: true,
              color: '#7E7986',
            },
            labels: {
              formatter: (value: number) => Math.floor(value),
              style: {
                colors: '#7E7986',
                fontSize: '12px',
                fontFamily: 'IBM Plex Mono',
                fontWeight: 400,
              },
            },
          },
          grid: {
            show: false,
          },
          tooltip: {
            custom: function ({ series, seriesIndex, dataPointIndex, w }) {
              return onToolTipShow
                ? onToolTipShow(dataPointIndex)
                : `<div>
                <span>
                ${series[seriesIndex][dataPointIndex]}: ${w}
                </span>
              </div>`;
            },
          },
        }}
        series={[
          {
            name: lineChartSerie.name,
            data: yValues,
          },
        ]}
        height={height}
        width="100%"
      />
    );
  }
}
