import React, { ReactNode } from 'react';
import BaseComponent from '../../../base/base-component';
import { SODGrade } from '../../../global/global-enum';
import { SODGradeColor } from '../../../global/grade-color-enum';
import LineChart from '../line/line-chart';
import { ILineChartData } from '../line/line-chart-interface';
import { ICycleScoreChartProperties } from './cycle-score-interface';

export default class CycleScoreChart extends BaseComponent<ICycleScoreChartProperties> {
  public render(): ReactNode {
    const { chartPoints, overallGrade } = this.props;
    const colorStops = this.populateColorStops();
    const lineChartData = chartPoints.map((point): ILineChartData => {
      return {
        x: point.cycle,
        y: point.score,
      };
    });
    return (
      <LineChart
        gradientColorStops={colorStops}
        height={200}
        width={400}
        lineChartSerie={{
          name: 'CycleScore',
          chartData: lineChartData,
        }}
        onToolTipShow={(pointIndex: number) => {
          return `<div class="c-cycle-score-tooltip">
                     <div class="c-cycle-score-tooltip__row">
                       <label class="c-cycle-score-tooltip__label">
                         Grade:
                       </label>
                       <div class="c-grade c-grade--tooltip" style="background-color: ${
                         SODGradeColor[overallGrade]
                       };">
                          ${overallGrade}
                       </div>
                     </div>

                     <div class="c-cycle-score-tooltip__row">
                       <label class="c-cycle-score-tooltip__label">
                         Year:
                       </label>
                       <div class="c-cycle-score-tooltip__text">
                          ${chartPoints[pointIndex].cycle}
                       </div>
                     </div>

                     <div class="c-cycle-score-tooltip__row">
                       <label class="c-cycle-score-tooltip__label">
                         Score:
                       </label>
                       <div class="c-cycle-score-tooltip__text">
                          ${Number(chartPoints[pointIndex].score).toFixed()}
                       </div>
                     </div>

                     <div class="c-cycle-score-tooltip__row">
                       <label class="c-cycle-score-tooltip__label">
                         Year Weight:
                       </label>
                       <div class="c-cycle-score-tooltip__text">
                          ${chartPoints[pointIndex].weight}
                       </div>
                     </div>
              </div>`;
        }}
      />
    );
  }

  private populateColorStops(): string[] {
    const { overallGrade } = this.props;

    if (
      overallGrade === SODGrade.GRADE_A ||
      overallGrade === SODGrade.GRADE_A_MINUS ||
      overallGrade === SODGrade.GRADE_A_PLUS
    )
      return [SODGradeColor['A+'], SODGradeColor['A-'], SODGradeColor.A];
    else if (
      overallGrade === SODGrade.GRADE_B ||
      overallGrade === SODGrade.GRADE_B_MINUS ||
      overallGrade === SODGrade.GRADE_B_PLUS
    )
      return [SODGradeColor['B+'], SODGradeColor['B-'], SODGradeColor.B];
    else if (
      overallGrade === SODGrade.GRADE_C ||
      overallGrade === SODGrade.GRADE_C_MINUS ||
      overallGrade === SODGrade.GRADE_C_PLUS
    )
      return [SODGradeColor['C+'], SODGradeColor['C-'], SODGradeColor.C];
    else if (
      overallGrade === SODGrade.GRADE_D ||
      overallGrade === SODGrade.GRADE_D_MINUS ||
      overallGrade === SODGrade.GRADE_D_PLUS
    )
      return [SODGradeColor['D+'], SODGradeColor['D-'], SODGradeColor.D];
    else return [SODGradeColor.F, SODGradeColor['D+']];
  }
}
